/* NotFound.css */
.not-found-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    text-align: center;
}

.error-message {
    font-size: 24px;
    margin-bottom: 20px;
}

.home-link {
    display: inline-block;
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    text-decoration: none;
    cursor: pointer;
}

.home-link:hover {
    background-color: #0056b3;
}

@media screen and (max-width: 768px) {
    .error-message {
        font-size: 20px;
    }

    .home-link {
        font-size: 16px;
    }
}