/* BackgroundAnimation.css */

.background-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
}

.background-circle {
    position: absolute;
    opacity: 0;
    border-radius: 50%;
    transform: scale(0);
}

.circle1 {
    width: 100px;
    height: 100px;
    top: -50px;
    left: 50%;
    animation: circleAnimation 5s infinite;
    animation-delay: 0s;
    /* Delay for circle1 */
    background: radial-gradient(circle, #ff9800, #f57c00, transparent);
}

.circle2 {
    width: 60px;
    height: 60px;
    top: 30%;
    left: 20%;
    animation: circleAnimation 5s infinite;
    animation-delay: 2s;
    /* Delay for circle2 */
    background: radial-gradient(circle, #e91e63, #c2185b, transparent);
}

.circle3 {
    width: 80px;
    height: 80px;
    top: 70%;
    left: 80%;
    animation: circleAnimation 5s infinite;
    animation-delay: 4s;
    /* Delay for circle3 */
    background: radial-gradient(circle, #2196f3, #1976d2, transparent);
}

/* Add more circles with different delays and colors */
.circle4 {
    width: 120px;
    height: 120px;
    top: 10%;
    left: 40%;
    animation: circleAnimation 5s infinite;
    animation-delay: 6s;
    /* Delay for circle4 */
    background: radial-gradient(circle, #4caf50, #388e3c, transparent);
}

.circle5 {
    width: 150px;
    height: 150px;
    top: 50%;
    left: 70%;
    animation: circleAnimation 5s infinite;
    animation-delay: 8s;
    /* Delay for circle5 */
    background: radial-gradient(circle, #9c27b0, #7b1fa2, transparent);
}

.circle6 {
    width: 90px;
    height: 90px;
    top: 10%;
    left: 70%;
    animation: circleAnimation 5s infinite;
    animation-delay: 10s;
    /* Delay for circle6 */
    background: radial-gradient(circle, #673ab7, #512da8, transparent);
}

/* Add more circles with different delays and colors */

@keyframes circleAnimation {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    50% {
        opacity: 0.7;
    }

    100% {
        transform: scale(1);
        opacity: 0;
    }
}

.circle7 {
    width: 70px;
    height: 70px;
    top: 80%;
    left: 10%;
    animation: circleAnimation 3.2s infinite;
    background: radial-gradient(circle, #03a9f4, #0288d1, transparent);
}

