/* index.css */

.result-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    text-align: center;
}

.result-title {
    font-size: 28px;
    margin-bottom: 20px;
}

.result-score {
    font-size: 24px;
    margin-bottom: 30px;
}

.retake-button,
.home-button {
    display: inline-block;
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    margin: 10px;
    text-decoration: none;
    cursor: pointer;
}

.retake-button:hover,
.home-button:hover {
    background-color: #0056b3;
}

@media screen and (max-width: 768px) {
    .result-title {
        font-size: 24px;
    }

    .result-score {
        font-size: 20px;
    }

    .retake-button,
    .home-button {
        font-size: 16px;
    }
}