/* styles/Register.css */

body {
    margin: 0;
    padding: 0;
    background-color: #f2f2f2;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.register-container {
    width: 400px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    text-align: center;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
    margin-bottom: 20px;
    font-size: 24px;
    color: #007bff;
}

.input-container {
    margin-bottom: 20px;
    text-align: left;
}

input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

label {
    margin-right: 10px;
}

button {
    padding: 10px 20px;
    font-size: 18px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.error-message {
    display: block;
    margin-top: 5px;
    color: red;
    font-size: 14px;
}

.success-message {
    margin-top: 10px;
    color: #4caf50;
}

/* Background Animation */
@keyframes circle-animation {
    0% {
        transform: scale(0);
        opacity: 1;
    }

    100% {
        transform: scale(2);
        opacity: 0;
    }
}

.background-circles {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.circle {
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    animation: circle-animation 2s infinite;
}

/* Define colors for the circles */
.circle-1 {
    background-color: #007bff;
}

.circle-2 {
    background-color: #ff4500;
}

.circle-3 {
    background-color: #4caf50;
}

.circle-4 {
    background-color: #ff00ff;
}

.circle-5 {
    background-color: #ff1493;
}

/* Responsive Layout Adjustments */
@media screen and (max-width: 600px) {
    .register-container {
        width: 90%;
    }

    input {
        font-size: 14px;
    }
}

@media screen and (max-width: 400px) {
    .register-container {
        width: 100%;
    }

    input {
        font-size: 12px;
    }
}

.login-here {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.error-message-display {
    margin-top: 5px;
    color: red;
    font-size: 15px;
}